h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
button {
  margin: 0;
  padding: 0;
}
*::after,
*::before {
  box-sizing: inherit;
}
li {
  list-style-type: none;
}
a,
address {
  text-decoration: none;
  display: block;
}
input:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}
img {
  display: block;
  max-width: 100%;
}